.page__headerWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: relative;
}

.page__backArrow {
  position: absolute;
  top: 20%;
  left: 5%;
}

.page__headerText {
  font-family: var(--font-family-primary);
  font-size: var(--header-font-size);
  padding: 0 var(--side-page-padding);
  margin: var(--header-y-padding) 0;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.page__headerLogo {
  width: 5rem;
  margin: 0 0.8rem;
}

.page__subHeader {
  font-family: var(--font-family-secondary);
  font-size: var(--subheader-font-size);
  padding: 3rem var(--side-page-padding);
  font-weight: 700;
  width: 100%;
  text-align: center;
  background: var(--color-secondary);
}

.page__filtersWrapper {
  font-family: var(--font-family-secondary);
  font-size: var(--subheader-font-size);
  padding: 3rem var(--side-page-padding);
  font-weight: 700;
  width: 100%;
  display: flex;
  justify-content: center;
  background: var(--color-secondary);
}

.page__filterOption {
  margin: 0 2em;
  cursor: pointer;
  text-align: center;
  border: none;
  background: none;
  font-family: var(--font-family-secondary);
  font-weight: bold;
  font-size: var(--subheader-font-size);
  transform: scale(1);
  transition: transform .2s ease-in;
}

.page__filterOption:hover {
  transform: scale(1.05);
}

.page__subHeaderText {
  margin: 0 2em;
  text-align: center;
}

.page__filterActive {
  text-decoration: underline;
}
