.recipeCard__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  border-radius: 0.4rem;
  background: var(--color-background);
  width: 20rem;
  height: 20rem;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
  cursor: pointer;
  position: relative;
  transition: transform 0.2s ease-in;
}

.recipeCard__wrapper:hover {
  transform: scale(1.1);
}

.recipeCard__headerWrapper {
  background: white;
  width: 100%;
  padding: 1rem 0;
  margin-bottom: 2rem;
  z-index: 1;
}

.recipeCard__header {
  font-family: var(--font-family-primary);
  text-align: center;
}

.recipeCard__img {
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: inherit;
}
