@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Poiret+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rokkitt:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  --color-text: black;
  --color-background: white;
  --color-background-hover: #f1f1f1;
  --color-primary: #e7ece6;
  --color-secondary: #f3f6f2;
  --font-family-primary: "Bebas Neue", cursive;
  --font-family-secondary: "Rokkitt", serif;
  --header-font-size: 7rem;
  --header-y-padding: 5rem;
  --subheader-font-size: 2.5rem;
  --side-page-padding: 7rem;
  --side-header-padding: 7rem;
  --back-arrow-width: 2rem;
  --paragraph-font-size: 2rem;
  --paragraph-line-height: 4rem;
  --recipe-content-margin: 0;
  --recipe-title-margin-left: 4rem;
  --recipe-header-margin: 5% 0;
  font-size: 62.5%;
}

@media only screen and (max-width: 600px) {
  html {
    --side-page-padding: 4rem;
    --header-font-size: 5rem;
    --header-y-padding: 3rem;
    --subheader-font-size: 1.6rem;
    --back-arrow-width: 1.5rem;
    --paragraph-font-size: 1.6rem;
    --paragraph-line-height: 3rem;
    --recipe-content-margin: 2rem 0;
    --recipe-title-margin-left: 2rem;
    --side-header-padding: 2rem;
    --recipe-header-margin: 0;
  }

  .recipeDetails__mealImage {
    display: none;
  }
}

button:focus {
  outline: 1px solid black;
  outline: 1px solid var(--color-text);
}

.App {
  font-size: 1.6rem;
}

.app__backArrow {
  cursor: pointer;
  width: auto;
  padding: 0 1.2rem;
  border: none;
  background: none;
}

.app__backArrowImg {
  width: 2rem;
  width: var(--back-arrow-width);
}

.app__cardsWrapper {
  display: flex;
  justify-content: center;
  background: #e7ece6;
  background: var(--color-primary);
  padding: 2rem 7rem;
  padding: 2rem var(--side-page-padding);
  margin-bottom: 3rem;
}

.app__cardsContainer {
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(auto-fit, minmax(22rem, 1fr));
  grid-gap: 6rem;
  margin: 4rem 0;
  width: 100%;
}

.app__infoSpan {
  font-family: "Rokkitt", serif;
  font-family: var(--font-family-secondary);
  font-size: 2rem;
}

.pageNotFound__Wrapper {
  width: 100%;
  margin-top: 6rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pageNotFound__Text {
  font-family: "Bebas Neue", cursive;
  font-family: var(--font-family-primary);
  font-size: 4rem;
}

.recipeDetails__headerWrapper {
  background: #e7ece6;
  background: var(--color-primary);
  padding: 4rem 7rem;
  padding: 4rem var(--side-header-padding);
  margin: 5% 0;
  margin: var(--recipe-header-margin);
  display: flex;
  align-items: center;
}

.recipeDetails__headerText {
  font-family: "Bebas Neue", cursive;
  font-family: var(--font-family-primary);
  font-size: 7rem;
  font-size: var(--header-font-size);
  margin-left: 4rem;
  margin-left: var(--recipe-title-margin-left);
}

.recipeDetails__contentWrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 2rem 7rem;
  padding: 2rem var(--side-page-padding);
}

.recipeDetails__recipeContent {
  display: flex;
  flex-direction: column;
  width: 40rem;
  margin: 0;
  margin: var(--recipe-content-margin);
}

.recipeDetails__recipeContentHeader {
  font-family: "Bebas Neue", cursive;
  font-family: var(--font-family-primary);
  font-size: 2.5rem;
}

.recipeDetails__contentSeparator {
  height: 2rem;
  width: 100%;
  border: none;
  background: #e7ece6;
  background: var(--color-primary);
}

.recipeDetails__ingredientsList {
  font-family: "Rokkitt", serif;
  font-family: var(--font-family-secondary);
  font-size: 2rem;
  font-size: var(--paragraph-font-size);
  font-weight: 700;
  line-height: 4rem;
  line-height: var(--paragraph-line-height);
}

.recipeDetails__instructionsText {
  font-family: "Rokkitt", serif;
  font-family: var(--font-family-secondary);
  font-size: 2rem;
  font-size: var(--paragraph-font-size);
  font-weight: 700;
  line-height: 4rem;
  line-height: var(--paragraph-line-height);
}

.recipeDetails__mealImage {
  width: 30rem;
  height: 30rem;
  border-radius: 0.6rem;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
}

.categoryCard__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border-radius: 0.4rem;
  padding: 2rem;
  background: var(--color-background);
  width: 20rem;
  height: 20rem;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
  cursor: pointer;
  transition: transform 0.2s ease-in;
}

.categoryCard__wrapper:hover {
  background: var(--color-background-hover);
  transform: scale(1.1);
}

.categoryCard__header {
  font-family: var(--font-family-primary);
  text-align: center;
  font-size: 2.5rem;
}

.categoryCard__imgWrapper {
  display: flex;
  justify-content: center;
}

.categoryCard__img {
  width: 14rem;
}

.page__headerWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: relative;
}

.page__backArrow {
  position: absolute;
  top: 20%;
  left: 5%;
}

.page__headerText {
  font-family: var(--font-family-primary);
  font-size: var(--header-font-size);
  padding: 0 var(--side-page-padding);
  margin: var(--header-y-padding) 0;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.page__headerLogo {
  width: 5rem;
  margin: 0 0.8rem;
}

.page__subHeader {
  font-family: var(--font-family-secondary);
  font-size: var(--subheader-font-size);
  padding: 3rem var(--side-page-padding);
  font-weight: 700;
  width: 100%;
  text-align: center;
  background: var(--color-secondary);
}

.page__filtersWrapper {
  font-family: var(--font-family-secondary);
  font-size: var(--subheader-font-size);
  padding: 3rem var(--side-page-padding);
  font-weight: 700;
  width: 100%;
  display: flex;
  justify-content: center;
  background: var(--color-secondary);
}

.page__filterOption {
  margin: 0 2em;
  cursor: pointer;
  text-align: center;
  border: none;
  background: none;
  font-family: var(--font-family-secondary);
  font-weight: bold;
  font-size: var(--subheader-font-size);
  transform: scale(1);
  transition: transform .2s ease-in;
}

.page__filterOption:hover {
  transform: scale(1.05);
}

.page__subHeaderText {
  margin: 0 2em;
  text-align: center;
}

.page__filterActive {
  text-decoration: underline;
}

.categoryCardLoader__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border-radius: 0.4rem;
  padding: 2rem;
  background: var(--color-background);
  width: 20rem;
  height: 20rem;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
  cursor: pointer;
}

.categoryCardLoader__header {
  font-family: var(--font-family-primary);
  text-align: center;
  font-size: 2rem;
}

.categoryCardLoader__imgWrapper {
  display: flex;
  justify-content: center;
}

.textCard__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border-radius: 0.4rem;
  padding: 2rem;
  background: var(--color-background);
  width: 20rem;
  height: 5rem;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
  cursor: pointer;
  transition: transform 0.2s ease-in;
}

.textCard__wrapper:hover {
  background: var(--color-background-hover);
  transform: scale(1.1);
}

.textCard__header {
  font-family: var(--font-family-primary);
  text-align: center;
  font-size: 2.5rem;
}

.textCard__imgWrapper {
  display: flex;
  justify-content: center;
}

.textCard__img {
  width: 14rem;
}

.recipeCard__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  border-radius: 0.4rem;
  background: var(--color-background);
  width: 20rem;
  height: 20rem;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
  cursor: pointer;
  position: relative;
  transition: transform 0.2s ease-in;
}

.recipeCard__wrapper:hover {
  transform: scale(1.1);
}

.recipeCard__headerWrapper {
  background: white;
  width: 100%;
  padding: 1rem 0;
  margin-bottom: 2rem;
  z-index: 1;
}

.recipeCard__header {
  font-family: var(--font-family-primary);
  text-align: center;
}

.recipeCard__img {
  width: 100%;
  height: 100%;
  position: absolute;
  border-radius: inherit;
}

.recipeCardLoader__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  border-radius: 0.4rem;
  background: var(--color-background);
  width: 20rem;
  height: 20rem;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 6px;
  cursor: pointer;
}

.recipeCardLoader__header {
  font-family: var(--font-family-primary);
  text-align: center;
  font-size: 2rem;
  padding: 1rem 0;
  margin-bottom: 2rem;
}

.recipeDetailsLoader__ingredientsList {
  margin: 1rem 0;
}

.recipeDetailsLoader__ingredients {
  margin: 0.5rem 0;
}

.recipeDetailsLoader__instructions {
  height: 6rem;
  margin: 1.5rem 0;
}

.recipeDetailsLoader__mealImageWrapper {
  display: flex;
  flex-direction: column;
}

.recipeDetailsLoader__mealImage {
  height: 30rem;
  width: 30rem;
}

